@charset "UTF-8";

@import "import/lib-fonts";
@import "base";
@import "import/typography";
@import "import/reset-css";
@import "datepicker";

html{-webkit-tap-highlight-color: transparent;}
/* blind */
.blind {@include blind();}
body.popView{ overflow: hidden; height: 100vh;}

.po-r{position: relative;}
.po-a{position: absolute;}

.ov-h{overflow: hidden;}
.ov-ha{overflow: hidden auto;}

.ta-center{text-align: center !important;}
.ta-left{text-align: left !important;}

.line_01{overflow: hidden; text-overflow: ellipsis; white-space: nowrap;}

.pc_only{display: block;}
.mo_only{display: none;}

.flex{}
.flex-between{display: flex; justify-content: space-between !important;}

.f-bold{font-weight: 700 !important;}
.f-medium{font-weight: 700 !important;}

.grid-col{display: grid; grid-template-columns: repeat(2, 1fr);}
.grid-col2{display: grid; grid-template-columns: repeat(3, 1fr);}

//
.bg-gray{background-color: #f4f5f8 !important;}
.inner{margin: 0 auto; padding-left: 48px; padding-right: 48px; max-width: 1416px;}

//color
.c-blue{color: $c-blue !important;}
.c-black{color: $c-black !important;}
.c-red{color: $c-red !important;}
.c-white{color: $c-white !important;}
.c-green{color: $c-green !important;}

.underline{text-decoration: underline;}
//.underline::after{content: ''; display: block; margin-top: 2px; width: 100%; height: 1px; background-color: $c-blue;}

//title
.page-tit{margin: 60px 0 48px; font-size: 40px; font-weight: 700; line-height: 1.15; color: $c-black; text-align: center;}
.page-tit h2{font-size: 40px; font-weight: 700; line-height: 1.15;}
.page-tit .desc{margin-top: 12px; font-size: 20px; font-weight: 400; line-height: 1.3; color: #7C828B;}

.sub-tit{margin-bottom: 32px;}
.sub-tit .tit{font-size: 32px; font-weight: 700; line-height: 1.2; color: $c-black;}
.sub-tit .desc{margin-top: 6px; font-size: 16px; color: #7c828b;}

.page-tit ~ .content-box{margin-top: 0;}

//btn
.btn-group{@include flex('center'); padding: 24px 0;}
.btn-group.end{justify-content: flex-end;}
.btn-group.row{flex-direction: column;}

.btn-group .btn + .btn{margin-left: 20px;}
.btn-group.row .btn + .btn{margin-left: 0; margin-top: 12px;}

.btn{display: inline-flex; align-items: center; justify-content: center; color: $c-black; font-size: 16px; font-weight: 500; border: 1px solid transparent; border-radius: 30px; background-color: transparent; cursor: pointer; user-select: none; box-sizing: border-box;}
.page-btn{@include flex('center'); padding: 40px 0 20px;}
.page-btn.row{flex-direction: column;}
.page-btn.row .btn + .btn{margin: 12px 0 0 0;}
.page-btn.row .btn + .btn.text{margin-top: 20px;}
.page-btn .btn{width: 312px; height: 60px; border-radius: 16px; background-color: $c-main; color: $c-white;}
.page-btn .btn + .btn{margin-left: 12px;}
.page-btn .btn.text{height: fit-content; font-size: 14px; font-weight: 500; color: #7C828B; background-color: transparent;}

.btn.bg-primary{background-color: $c-main; border-color: $c-main; color: $c-white;}
.btn.bg-secondary{background-color: $c-skyblue; border-color: $c-skyblue; color: $c-black;}
.btn.bg-black{background-color: #141618; border-color: #141618; color: $c-white;}
.btn.bg-white{background-color: $c-white !important;}
.btn.bg-primary .icon{background-color: $c-white;}
.btn.bg-black .icon{background-color: $c-white;}
.btn.bg-red{background-color: #EA463D;}

.btn.line{border-color: $c-gray-5; background-color: transparent; color: $c-gray-1; }
.btn.line.c-primary{border-color: $c-main; color: $c-main;}
.btn-l.square{border-radius: 16px;}
.btn-m.square{border-radius: 12px;}
.btn-s.square{border-radius: 8px;}
.btn-xs.square{border-radius: 6px;}

.btn.text{font-size: 16px; font-weight: 500; line-height: 1.5; color: $c-main;}
.btn.text-m{font-size: 14px; font-weight: 500; line-height: 1.3; color: #7c828b;}
.btn.underline{position: relative;}
.btn.underline::after{content: ''; position: absolute; left: 0; bottom: 0; display: block; width: 100%; height: 1px; background-color: $c-main;}

.btn.disabled{background-color: $c-gray-7; border-color: $c-gray-7; color: $c-gray-6; cursor: initial;}

.btn-l{padding: 0 60px; height: 60px;}
.btn-m{padding: 0 24px; height: 48px;}
.btn-s{padding: 0 16px; height: 36px;}
.btn-xs{padding: 0 10px; height: 28px;}

.btn.full{width: 100%;}
.btn.wid-l{width: 312px; box-sizing: border-box;}

.btn .icon{flex-shrink: 0; width: 20px; height: 20px;}
.btn .icon ~ span{margin-left: 6px;}
.btn span ~ .icon{margin-left: 6px;}

//tab
.tab-list{@include flex();}
.tab-list li{flex: 1; height: 68px;}
.tab-list li button{@include flex('center'); width: 100%; height: 100%; color: $c-gray-4; font-size: 20px; font-weight: 500; line-height: 1.6; cursor: pointer;}
.tab-list li.active button{font-weight: 700; color: $c-black;}
.tab-cont{display: none;}

.tab-line{margin-bottom: 32px; border-bottom: 1px solid $c-gray-7;}
.tab-line li.active{border-bottom: 3px solid $c-main;}

.tab-round li{margin-right: 12px; height: fit-content;}
.tab-round li:last-child{margin-right: 0;}
.tab-round li button{padding: 8px 16px 8px 8px; font-size: 16px; border-radius: 24px; background-color: #fff; white-space: nowrap;}

.tab-round li.active button{font-weight: 700; color: #fff; background-color: #141618;}
.tab-round .tab-items{display: flex; align-items: center;}
.tab-round .sort-select li{margin-right: 0;}

//icon
.icon{--bg-color: #{$c-gray-1}; width: 16px; height: 16px;
  display: inline-flex; align-items: center; justify-content: center; background-color: var(--bg-color);
}

.icon-plus{@include mask('common/icon_plus.svg');}
.icon-question{@include mask('common/icon_question.svg', $c-main);}
.icon-left{@include mask('common/icon_left.svg', $c-black);}
.icon-right{@include mask('common/icon_right.svg', $c-black);}
.icon-list{@include mask('common/icon_list.svg', $c-black);}
.icon-arrow-right{@include mask('common/icon_arrow_right.svg', $c-black);}
.icon-search{@include mask('common/ico_search.svg', $c-gray-4);}
.icon-upload{@include mask('common/ico_upload.svg', $c-gray-3);}

.icon-all{background-image: url('../img/common/ico_tab_all.svg');}
.icon-special{background-image: url('../img/common/ico_tab_special.svg');}
.icon-plan{background-image: url('../img/common/ico_tab_plan.svg');}
.icon-employ{background-image: url('../img/common/ico_tab_employ.svg');}
.icon-founded{background-image: url('../img/common/ico_tab_founded.svg');}
.icon-donation{background-image: url('../img/common/ico_tab_donation.svg');}
.icon-etc{background-image: url('../img/common/ico_tab_etc.svg');}
.icon-social{background-image: url('../img/common/ico_tab_social.svg');}
.icon-learning{background-image: url('../img/common/ico_tab_learning.svg');}
.icon-finance{background-image: url('../img/common/ico_tab_finance.svg');}

.icon.size-s{width: 16px; height: 16px;}
.icon.size-m{width: 20px; height: 20px;}
.icon.size-sm{width: 24px; height: 24px;}
.icon.size-l{width: 40px; height: 40px;}
.icon.size-xl{width: 60px; height: 60px;}

//accordion
.accordion .detail{display: none;}

//badge
.badge{padding: 6px 10px; font-size: 14px; font-weight: 500; line-height: 1.7; border-radius: 6px;}
.badge.small{padding: 3px 6px; font-size: 12px; font-weight: 400; line-height: 1.5;}
.badge.success{background-color: #e6fff3; color: $c-green;}
.badge.fail{background-color: #fae9e7; color: $c-red;}
.badge.active{}
.badge.bg-blue{background-color: $c-blue; color: $c-white;}
.badge.bg-black{background-color: $c-black; color: $c-white;}
.badge.bg-gray{background-color: #F4F5F8; color: #7C828B;}

//input
.inp-list{display: grid; gap: 16px;}
.inp-list.gap-l{gap: 32px;}

.inp-label{position: relative; display: flex; align-items: center; margin-bottom: 6px; font-size: 14px; color: $c-gray-3; line-height: 1.3; cursor: initial;}
.required .inp-label::after{content: '*'; display: inline-block; margin-left: 4px; font-size: 14px; color: $c-red;}

.inp-group.no-wrap{flex-wrap: nowrap !important;}

.inp .inp-group{@include flex();}
.inp .inp-group .btn{flex-shrink: 0; margin-left: 12px; border-radius: 12px;}
.inp .inp-area{display: flex; align-items: center; padding: 12px 16px; width: 100%; height: 48px; border-radius: 12px; border: 1px solid $c-gray-6; background-color: $c-white;}
.inp .inp-area input{width: 100%; font-size: 16px; color: $c-gray-1;}
.inp .inp-area input::placeholder{color: $c-gray-5; font-weight: 400;}
.inp .inp-area:focus-within{border-color: $c-blue;}
.inp .inp-area .badge{flex-shrink: 0; margin-right: -8px; margin-left: 12px; padding: 6px 10px; font-size: 14px; font-weight: 500; line-height: 1.7;}
.inp .desc-error{display: none; margin-top: 4px; font-size: 14px; color: $c-red;}

.inp.textarea .inp-area{align-items: flex-start; height: 264px;}
.inp.textarea .inp-area textarea{width: 100%; height: 100%;}
.inp.textarea .inp-area textarea::placeholder{font-size: 16px; color: #b9c0c6; font-weight: 400;}

.inp.error .inp-area{border-color: $c-red;}
.inp.error .desc-error{display: block;}
.inp.disabled .inp-area{color: #d8dde2; border-color: #f4f5f8; background-color: #f4f5f8;}
.inp.readonly .inp-area{color: #141618; border-color: #f4f5f8; background-color: #f4f5f8;}


.inp-checkbox-list .inp-checkbox + .inp-checkbox{margin-top: 16px;}
.inp-checkbox-list.line::before{content: ''; display: block; margin: 32px 0 24px; width: 100%; height: 1px; background-color: #d8dde2;}
.inp-checkbox input{@include blind();}
.inp-checkbox .inp-label{cursor: pointer;}
.inp-checkbox .inp-label::before{content: ''; flex-shrink: 0; display: inline-block; margin-right: 8px; width: 24px; height: 24px; background-color: $c-white; border: 1px solid #ddd; border-radius: 6px; box-sizing: border-box;}
.inp-checkbox [type=checkbox]:checked + .inp-label::before{border-color: $c-main; @include bg('common/ico_checkbox.svg'); background-color: $c-main;}
.inp-checkbox [type=checkbox]:disabled + .inp-label::before{}
.inp-checkbox [type=checkbox]:read-only + .inp-label::before{}
.inp-checkbox .inp-label .inp-checkbox-text{width: calc(100% - 32px); font-size: 16px; color: $c-black; line-height: 1.25;}
.inp-checkbox .inp-label .btn-detail{position: relative; margin-left: auto; flex-shrink: 0; z-index: 2;}
.inp-checkbox .inp-label .btn-detail::after{content: ''; display: inline-block; width: 16px; height: 16px; @include bg('common/icon_arrow_right.svg');}

.inp-checkbox-list.type2 .inp-checkbox + .inp-checkbox{margin-top: 0;}
.inp-checkbox-list.type2 .inp-checkbox .inp-label{font-weight: 400; color: #7C828B;}
.inp-checkbox-list.type2 .inp-checkbox .inp-label::before{margin-right: 6px; width: 20px; height: 20px; @include mask('common/ico_checkbox.svg'); border-color: $c-white; background: #D8DDE2;}
.inp-checkbox-list.type2 .inp-checkbox [type=checkbox]:checked + .inp-label{font-weight: 500; color: #2E3337;}
.inp-checkbox-list.type2 .inp-checkbox [type=checkbox]:checked + .inp-label::before{ border: none; background: $c-main;}
.inp-checkbox-list.type2 .inp-checkbox [type=checkbox]:disabled + .inp-label::before{}

.inp-radio-list{@include flex(); margin: 12px 0 -12px -20px;}
.inp-radio-list .inp-radio{margin-left: 20px; margin-bottom: 12px;}

.inp-radio{position: relative; --bg: #{$c-white}; --border: #D8DDE2; cursor: pointer;}
.inp-radio input{@include blind();}
.inp-radio .inp-label{font-size: 16px; color: $c-black; cursor: pointer;}
.inp-radio .inp-label::before{content: ''; display: inline-block; margin-right: 8px; width: 24px; height: 24px; border: 1px solid var(--border); border-radius: 50%; background-color: var(--bg); box-sizing: border-box;}
.inp-radio .inp-label::after{content: ''; display: inline-block; position: absolute; top: 50%; left: 7px; transform: translateY(-50%); width: 10px; height: 10px; border-radius: 50%; background-color: $c-white;}
.inp-radio .inp-label .desc-red{margin-left: 8px; font-size: 14px; color: $c-red;}
.inp-radio [type=radio]:checked + .inp-label::before{--bg: #{$c-main}; --border: #{$c-main}; color: $c-main;}
.inp-radio [type=radio]:disabled + .inp-label::before{}

.inp-radio-list.type2{margin: 0 0 0 12px; flex-shrink: 0;}
.inp-radio-list.type2 .inp-radio{margin: 0;}
.inp-radio-list.type2 .inp-radio:focus-within{position: relative; z-index: 1;}
.inp-radio-list.type2 .inp-radio + .inp-radio{margin-left: -1px;}
.inp-radio-list.type2 .inp-radio .inp-label{@include flex('center'); margin-bottom: 0; width: 78px; height: 48px; color: #7c828b; border: 1px solid #B9C0C6; border-radius: 12px;}
.inp-radio-list.type2 .inp-radio:first-child .inp-label{border-radius: 12px 0 0 12px;}
.inp-radio-list.type2 .inp-radio:last-child .inp-label{border-radius: 0 12px 12px 0;}
.inp-radio-list.type2 .inp-radio .inp-label::before{display: none;}
.inp-radio-list.type2 .inp-radio .inp-label::after{display: none;}
.inp-radio-list.type2 .inp-radio [type=radio]:checked + .inp-label{position: relative; border-width: 2px; border-color: $c-main; color: $c-main;}
.inp-radio-list.type2 .inp-radio [type=radio]:disabled + .inp-label::before{}

.inp-radio-list.type3{display: grid; grid-template-columns: repeat(3, 1fr); gap: 12px; margin: 0;}
.inp-radio-list.type3 .inp-radio{margin: 0;}
.inp-radio-list.type3 .inp-radio.full{grid-column: 1/-1;}
.inp-radio-list.type3 .inp-radio .inp-label{@include flex('center'); margin-bottom: 0; width: 100%; height: 48px; font-size: 14px; color: #2E3337; border: 1px solid #F4F5F8; background-color: #F4F5F8; border-radius: 9px;}
.inp-radio-list.type3 .inp-radio .inp-label::before{display: none;}
.inp-radio-list.type3 .inp-radio .inp-label::after{display: none;}
.inp-radio-list.type3 .inp-radio [type=radio]:checked + .inp-label{font-weight: 500; border-width: 2px; border-color: $c-main; background-color: $c-white;}
.inp-radio-list.type3 .inp-radio [type=radio]:disabled + .inp-label::before{}

.inp-select{position: relative; font-size: 16px;}
.inp-select .inp-btn{position: relative; @include flex('flex-start'); padding: 0 20px; width: 100%; height: 48px; border: 1px solid #d8dde2; border-radius: 12px; box-sizing: border-box;}
.inp-select .inp-btn::after{content: ''; display: inline-block; margin-left: auto; flex-shrink: 0; width: 20px; height: 20px; @include mask('common/icon_arrow_down.svg', #2E3337); }
.inp-select .inp-btn:focus-within{border-color: $c-blue;}
.inp-select .inp-btn.active::after{transform: rotate(-180deg);}
.inp-select .label{margin-right: 12px;}
.inp-select .placeholder{margin-right: 8px; color: #B9C0C6;}
.inp-select .value{margin-right: 8px;}
.inp-select .inp-options{display: none; position: absolute; margin-top: 4px; padding: 12px 6px; width: 100%; border-radius: 16px; border: 1px solid #d8dde2; background-color: #fff; box-shadow: 0 5px 12px 0 rgba(0, 37, 106, 0.1); z-index: 9;}
.inp-select .inp-options .option{padding: 8px 11px; border-radius: 8px; cursor: pointer; white-space: nowrap;}
.inp-select .inp-options .option:hover{background-color: #f4f5f8;}
.inp-select .inp-options .option a{display: block; width: 100%; height: 100%;}
.inp-select .inp-tit{margin-bottom: 12px; font-size: 20px; font-weight: 700; color: $c-black;}

.inp-select.text .inp-btn{padding: 0 10px; height: fit-content; border: none;}
.inp-select.text .inp-btn::after{--bg-color: #{#7C828B};}

.inp.calendar .inp-area{padding: 0; max-width: 200px;}
.inp.calendar .inp-area .datepicker{padding: 12px 16px;}
.inp.calendar .dash{flex-shrink: 0; margin: 0 12px; width: 7px; height: 1px; background-color: #141618;}
.inp.calendar .date-txt{flex-shrink: 0; margin-left: 12px; font-size: 14px; color: #000;}

.white-box{padding: 40px; background: $c-white; border-radius: 24px; box-shadow: 0 3px 6px 0 rgba(0, 37, 106, 0.05);}

/* space */
$position: (t:top, b:bottom, r:right, l: left);
$total: 10;

@each $abbr, $pos in $position {
  @for $i from 0 to $total {
    .m#{$abbr}-#{$i} {margin-#{$pos}: $i*10px !important;}
    .p#{$abbr}-#{$i} {padding-#{$pos}: $i*10px !important;}
  }
}

@media all and (max-width:960px){
  .white-box{padding: 0; box-shadow: none;}
}

@media all and (max-width:720px){
  .pc_only{display: none;}
  .mo_only{display: block;}

    /* space */
    $position: (t:top, b:bottom, r:right, l: left);
    $total: 10;

    @each $abbr, $pos in $position {
    @for $i from 0 to $total {
        .m#{$abbr}-#{$i} {margin-#{$pos}: $i*1.39vw !important;}
        .p#{$abbr}-#{$i} {padding-#{$pos}: $i*1.39vw !important;}
    }
    }

  //title
  .page-tit{margin: 8.89vw 0; font-size: 6.67vw; font-weight: 700; text-align: left;}
  .page-tit h2{font-size: 6.67vw;}
  .page-tit .desc{margin-top: 1.67vw; font-size: 3.89vw;}
  .sub-tit{margin-bottom: 6.67vw;}
  .sub-tit .tit{font-size: 6.67vw;}
  .sub-tit .desc{margin-top: 1.67vw; font-size: 3.33vw;}

  //btn
  .btn-group{padding: 6.67vw 0;}

  .btn-group .btn + .btn{margin-left: 2.78vw;}
  .btn-group.row .btn + .btn{margin-top: 3.33vw;}

  .btn{border-width: 0.28vw; border-radius: 6.67vw;}
  .page-btn{padding: 5.56vw 0 2.78vw; width: 100%;}
  .page-btn.row .btn + .btn{margin: 3.33vw 0 0 0;}
  .page-btn.row .btn + .btn.text{margin-top: 4.44vw;}
  .page-btn .btn{width: 100%; height: 13.33vw; font-size: 4.44vw; border-radius: 3.33vw; }
  .page-btn .btn.text{font-size: 3.89vw;}
  .page-btn .btn + .btn{margin-left: 3.33vw;}

  .page-btn.mo-line{display: block;}
  .page-btn.mo-line .btn + .btn{margin: 3.33vw 0 0 0;}

  .btn-l.square{border-radius: 3.33vw;}
  .btn-m.square{border-radius: 2.78vw;}
  .btn-s.square{border-radius: 1.67vw;}
  .btn-xs.square{border-radius: 1.67vw;}

  .btn.text{font-size: 3.33vw;}
  .btn.text-m{font-size: 3.89vw;}
  .btn.underline::after{height: 0.28vw;}

  .btn-l{padding: 0 6.67vw; height: 13.33vw; font-size: 4.44vw;}
  .btn-m{padding: 0 5.56vw; height: 11.11vw; font-size: 3.89vw;}
  .btn-s{padding: 0 3.33vw; height: 3.89vw; font-size: 3.33vw;}
  .btn-xs{padding: 0 3.33vw; height: 3.89vw; font-size: 3.33vw;}

  .btn .icon{width: 4.44vw; height: 4.44vw;}
  .btn .icon ~ span{margin-left: 1.11vw;}
  .btn span ~ .icon{margin-left: 1.11vw;}

  //tab
  .tab-list li{height: 13.33vw;}
  .tab-list li button{font-size: 4.44vw;}

  .tab-line{margin: 0 -6.67vw 8.89vw; border-bottom-width: 0.28vw;}
  .tab-line li.active{border-bottom-width: 0.56vw;}

  .tab-round li{margin-right: 1.67vw; height: fit-content;}
  .tab-round li button{padding: 1.67vw 3.33vw 1.67vw 1.67vw; font-size: 4.44vw; border-radius: 6.67vw;}

  //icon
  .icon.size-s{width: 4.44vw; height: 4.44vw;}
  .icon.size-m{width: 5.56vw; height: 5.56vw;}
  .icon.size-sm{width: 6.67vw; height: 6.67vw;}
  .icon.size-l{width: 11.11vw; height: 11.11vw;}
  .icon.size-xl{width: 16.67vw; height: 16.67vw;}

  //accordion

  //badge
  .badge{padding: 0.83vw 2.22vw; font-size: 3.33vw; line-height: 1.7; border-radius: 1.67vw;}
  .badge.small{padding: 0.83vw 1.67vw; font-size: 3.33vw;}

  //input
  .inp-list{gap: 4.44vw;}
  .inp-list.gap-l{gap: 6.67vw;}

  .inp-label{margin-bottom: 1.67vw; font-size: 3.33vw;}

  .required .inp-label::after{margin-left: 1.11vw; font-size: 3.33vw;}

  .inp .inp-group{flex-wrap: wrap;}
  .inp .inp-group .btn{margin-left: 1.67vw; border-radius: 2.78vw;}
  .inp .inp-area{padding: 2.78vw 3.33vw; height: 11.11vw; border-radius: 2.78vw; border-width: 0.28vw;}
  .inp .inp-area input{font-size: 3.33vw;}
  .inp .inp-area .badge{margin-right: -1.11vw; margin-left: 3.33vw;}
  .inp .desc-error{margin-top: 1.11vw; font-size: 3.33vw;}

  .inp.textarea .inp-area{height: 31.11vw;}
  .inp.textarea .inp-area textarea{font-size: 3.33vw;}
  .inp.textarea .inp-area textarea::placeholder{font-size: 3.33vw;}

  .inp-checkbox-list .inp-checkbox + .inp-checkbox{margin-top: 4.44vw;}
  .inp-checkbox-list.line::before{margin: 6.67vw 0; height: 0.28vw;}
  .inp-checkbox .inp-label{flex-wrap: wrap; align-items: flex-start;}
  .inp-checkbox .inp-label::before{margin-right: 1.67vw; width: 5.56vw; height: 5.56vw; border-width: 0.28vw; border-radius: 1.11vw;}
  .inp-checkbox .inp-label .inp-checkbox-text{padding-top: 0.28vw; width: calc(100% - 7.23vw); font-size: 3.89vw;}
  .inp-checkbox .inp-label .btn-detail{justify-content: flex-start; padding: 1.67vw 0 0 7.22vw; width: 100%; font-size: 3.89vw;}
  .inp-checkbox .inp-label .btn-detail::after{margin-left: 0.56vw; width: 4.44vw; height: 4.44vw;}

  .inp-checkbox-list.type2 .inp-checkbox .inp-label::before{margin-right: 1.11vw; width: 4.44vw; height: 4.44vw;}

  .inp-radio-list{flex-wrap: wrap; margin: 3.33vw 0 -4.44vw -6.67vw;}
  .inp-radio-list .inp-radio{margin-left: 6.67vw; margin-bottom: 4.44vw;}

  .inp-radio .inp-label{font-size: 3.89vw;}
  .inp-radio .inp-label::before{margin-right: 1.67vw; width: 5.56vw; height: 5.56vw; border-width: 0.28vw;}
  .inp-radio .inp-label::after{left: 1.67vw; width: 2.22vw; height: 2.22vw;}
  .inp-radio .inp-label .desc-red{margin-left: 1.67vw; font-size: 3.33vw;}

  .inp-radio-list.type2{margin-top: 3.33vw; margin-left: 0; width: 100%;}
  .inp-radio-list.type2 .inp-radio{flex: 1;}
  .inp-radio-list.type2 .inp-radio .inp-label{margin-bottom: 0; width: 100%; height: 11.11vw; border-width: 0.28vw; border-radius: 2.78vw;}
  .inp-radio-list.type2 .inp-radio:first-child .inp-label{border-radius: 2.78vw 0 0 2.78vw;}
  .inp-radio-list.type2 .inp-radio:last-child .inp-label{border-radius: 0 2.78vw 2.78vw 0;}
  .inp-radio-list.type2 .inp-radio [type=radio]:checked + .inp-label{border-width: 0.56vw;}

  .inp-radio-list.type3{grid-template-columns: repeat(2, 1fr); gap: 3.33vw 4.44vw;}
  .inp-radio-list.type3 .inp-radio .inp-label{height: 13.33vw; font-size: 3.89vw; border-width: 0.28vw; border-radius: 2.50vw;}

  .inp-select{font-size: 3.89vw;}
  .inp-select .inp-btn{padding: 0 3.33vw; height: 11.11vw; border-width: 0.28vw; border-radius: 2.78vw;}
  .inp-select .inp-btn::after{width: 4.44vw; height: 4.44vw;}

  .inp.calendar .inp-group{flex-wrap: nowrap;}
  .inp.calendar .inp-area{max-width: fit-content; width: 100%;}
  .inp.calendar .dash{margin: 0 1.67vw; width: 1.67vw; height: 0.28vw;}
  .inp.calendar .date-txt{margin-left: auto; font-size: 3.33vw; color: #7c828b;}

  .select-pop .pop-inner{position: absolute; bottom: 6.67vw; width: calc(100% - 13.34vw); background-color: transparent; border-radius: 0;}
  .select-pop .pop-inner::before{display: none;}
  .select-pop .inp-options{padding: 3.33vw; border-radius: 3.33vw; background-color: $c-white;}
  .select-pop .inp-options .option{@include flex('center'); width: 100%; height: 13.33vw; font-size: 3.89vw; font-weight: 500; color: $c-black; cursor: pointer;}
  .select-pop .inp-options .option a{display: flex; width: 100%; height: 100%; justify-content: center; align-items: center;}
  .select-pop .inp-options .btn.text{justify-content: center; width: 100%; height: 13.33vw; font-size: 3.89vw;}
  .select-pop .inp-options .btn.text .icon{background-color: $c-blue;}
  .select-pop .pop-btn{margin-top: 3.33vw; width: 100%;}
  .select-pop .pop-btn .btn{color: $c-blue; font-size: 3.89vw; background-color: $c-white;}

  .white-box{border-radius: 0;}

}