#datepicker:focus > .ui-datepicker {
	display: block;
}

.ui-widget.ui-widget-content {
	border: 1px solid #d8dde2;
}

.ui-widget-header {
	border: 0 solid #dddddd;
	background: #fff;
}

.ui-widget-header .ui-icon {
	background-image: none;
}

.ui-state-default, .ui-widget-content .ui-state-default, .ui-widget-header .ui-state-default, .ui-button, .ui-button.ui-state-disabled:hover, .ui-button.ui-state-disabled:active {
	border: none;
	background-color: transparent;
	font-weight: normal;
	color: #454545;
	text-align: center;
}

.ui-state-hover, .ui-widget-content .ui-state-hover, .ui-widget-header .ui-state-hover, .ui-state-focus, .ui-widget-content .ui-state-focus, .ui-widget-header .ui-state-focus, .ui-button:hover, .ui-button:focus {
	border: none;
	background-color: transparent;
	font-weight: normal;
	color: #2b2b2b;
}

.ui-state-highlight, .ui-widget-content .ui-state-highlight, .ui-widget-header .ui-state-highlight {
	position: relative;
	border: 0;
}

.ui-state-highlight::before, .ui-widget-content .ui-state-highlight::before, .ui-widget-header .ui-state-highlight::before {
	z-index: -1;
	content: "";
	position: absolute;
	left: 50%;
	top: 50%;
	transform: translate(-50%, -50%);
	padding-top: 100%;
	width: 100%;
	background: #f4f5f8;
	border-radius: 4px;
}

.ui-state-disabled {
	cursor: auto;
	color: #cccccc;
}

.ui-datepicker {
	display: none;
	margin: 4px 0 0;
	padding: 20px 24px 24px;
	width: 292px;
	border-radius: 16px;
	box-shadow: 0 8px 20px 0 rgba(0, 0, 0, 0.1);
	background-color: #fff;
}

.ui-datepicker .inp {
	padding: 10px 10px;
	background-color: #f1f1f1;
	border-radius: 4px;
	border: 0;
}

.ui-datepicker .inp:focus {
	outline: none;
	background-color: #eee;
}

.ui-datepicker .ui-datepicker-header {
	display: flex;
	align-items: center;
	justify-content: center;
	padding: 0 0 24px;
}

.ui-datepicker .ui-datepicker-title {
