.inp-checkbox-list .inp-checkbox + .inp-checkbox {
	margin-top: 16px;
}

.inp-checkbox-list.line::before {
	content: "";
	display: block;
	margin: 32px 0 24px;
	width: 100%;
	height: 1px;
	background-color: #d8dde2;
}

.inp-checkbox input {
	overflow: hidden;
	border: 0;
	position: absolute;
	z-index: -1;
	width: 1px;
	height: 1px;
	clip: rect(1px, 1px, 1px, 1px);
	clip-path: inset(50%);
}

.inp-checkbox .inp-label {
	cursor: pointer;
}

.inp-checkbox .inp-label::before {
	content: "";
	flex-shrink: 0;
	display: inline-block;
	margin-right: 8px;
	width: 24px;
	height: 24px;
	background-color: #fff;
	border: 1px solid #ddd;
	border-radius: 6px;
	box-sizing: border-box;
}

.inp-checkbox [type=checkbox]:checked + .inp-label::before {
	border-color: #3575F6;
	background: url("../img/common/ico_checkbox.svg") no-repeat center/100% auto;
	background-color: #3575F6;
}

.inp-checkbox .inp-label .inp-checkbox-text {
	width: calc(100% - 32px);
	font-size: 16px;
	color: #000;
	line-height: 1.25;
}

.inp-checkbox .inp-label .btn-detail {
	position: relative;
	margin-left: auto;
	flex-shrink: 0;
	z-index: 2;
}

.inp-checkbox .inp-label .btn-detail::after {
	content: "";
	display: inline-block;
	width: 16px;
	height: 16px;
	background: url("../img/common/icon_arrow_right.svg") no-repeat center/100% auto;
}

.inp-checkbox-list.type2 .inp-checkbox + .inp-checkbox {
	margin-top: 0;
}

.inp-checkbox-list.type2 .inp-checkbox .inp-label {
	font-weight: 400;
	color: #7C828B;
}

.inp-checkbox-list.type2 .inp-checkbox .inp-label::before {
	margin-right: 6px;
	width: 20px;
	height: 20px;
	--bg-color: #141618;
	mask-image: url("../img/common/ico_checkbox.svg");
	-webkit-mask-clip: content;
	-webkit-mask-size: contain;
	-webkit-mask-repeat: no-repeat;
	-webkit-mask-position: center;
	background-color: var(--bg-color);
	border-color: #fff;
	background: #D8DDE2;
}

.inp-checkbox-list.type2 .inp-checkbox [type=checkbox]:checked + .inp-label {
	font-weight: 500;
	color: #2E3337;
}

.inp-checkbox-list.type2 .inp-checkbox [type=checkbox]:checked + .inp-label::before {
	border: none;
