.inp-radio-list .inp-radio {
	margin-left: 20px;
	margin-bottom: 12px;
}

.inp-radio {
	position: relative;
	--bg: #fff;
	--border: #D8DDE2;
	cursor: pointer;
}

.inp-radio input {
	overflow: hidden;
	border: 0;
	position: absolute;
	z-index: -1;
	width: 1px;
	height: 1px;
	clip: rect(1px, 1px, 1px, 1px);
	clip-path: inset(50%);
}

.inp-radio .inp-label {
	font-size: 16px;
	color: #000;
	cursor: pointer;
}

.inp-radio .inp-label::before {
	content: "";
	display: inline-block;
	margin-right: 8px;
	width: 24px;
	height: 24px;
	border: 1px solid var(--border);
	border-radius: 50%;
	background-color: var(--bg);
	box-sizing: border-box;
}

.inp-radio .inp-label::after {
	content: "";
	display: inline-block;
	position: absolute;
	top: 50%;
	left: 7px;
	transform: translateY(-50%);
	width: 10px;
	height: 10px;
	border-radius: 50%;
	background-color: #fff;
}

.inp-radio .inp-label .desc-red {
	margin-left: 8px;
	font-size: 14px;
	color: #EA463C;
}

.inp-radio [type=radio]:checked + .inp-label::before {
	--bg: #3575F6;
	--border: #3575F6;
	color: #3575F6;
}

.inp-radio-list.type2 {
	margin: 0 0 0 12px;
	flex-shrink: 0;
}

.inp-radio-list.type2 .inp-radio {
	margin: 0;
}

.inp-radio-list.type2 .inp-radio:focus-within {
	position: relative;
	z-index: 1;
}

.inp-radio-list.type2 .inp-radio + .inp-radio {
	margin-left: -1px;
}

.inp-radio-list.type2 .inp-radio .inp-label {
